.main__wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: calc(100% - 2 * 1.5rem);
  max-width: 62.5rem;
}
.main__content {
  background-color: #1967d2;
  border-radius: 0.5rem;
  margin-top: 1.5rem;
  overflow: hidden;
}
.main__wrapper1 {
  height: 15rem;
  position: relative;
  width: 100%;
  height: 100%;
}
.main__bgImage {
  background-image: url(https://t3.ftcdn.net/jpg/01/57/33/98/360_F_157339827_syL1TxmVCCUHitdSx56LnVV3YnsJn2EW.jpg);
  background-size: cover;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.main__emptyStyles {
  display: block;
